body, html{
    padding: 0;
    margin: 0;
}

body{
    width: 80%;
    min-width: 960px;
    margin: 0 auto;
}

.flex-center{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.register-form .flex-center{
    padding: 20px;
}

.register-form{
    width: 600px;
    height: 450px;
    border: 2px solid rgba(126, 85, 255, 0.62);
    background: rgba(195, 217, 255, 0.8);
    margin: 10% auto;
    padding: 50px;
    border-radius: 15px;
}

.register-form h1{
    text-align: center;
    font-size: 2.5em;
    color: #282828;
    font-family: Monaco Full Stack, Times, serif;
}

.register-form input{
    width: 490px;
    padding: 10px;
    margin: 30px 0;
}

.register-form a{
    font-size: 1.3em;
}

.password{
    color: #575757;
}

.true{
    text-decoration: line-through;
}
.th{
    text-align: right;
    border-spacing: 10px;
}
.input{
    height: 35px;
}
.save-button{
    height: 35px;
}


